    h1 {
      font-size: 32px; } }
  @media (min-width: 1600px) {
    h1 {
      font-size: 38px; } }

.backToTopBtn {
  display: block;
  align-self: center;
  position: absolute;
  bottom: 15px;
  background: transparent;
  border: none;
  outline: 0;
  text-align: center;
  cursor: pointer;
  -webkit-animation: up 2s infinite;
          animation: up 2s infinite; }
  .backToTopBtn span {
    font-size: 12px;
    display: block; }
    @media (min-width: 1024px) {
      .backToTopBtn span {
        font-size: 18px; } }
  .backToTopBtn i {
    color: black; }
    .backToTopBtn i:before {
      font-size: 25px; }
    @media (min-width: 600px) {
      .backToTopBtn i:before {
        font-size: 30px; } }
    @media (min-width: 768px) {
      .backToTopBtn i:before {
        font-size: 35px; } }
    @media (min-width: 1024px) {
      .backToTopBtn i:before {
        font-size: 40px; } }

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1); }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2); }
  10% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1); }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2); }
  10% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@-webkit-keyframes up {
  0%,
  20%,
  50%,
  80%,
  100% {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px); }
  40% {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px); }
  60% {
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@keyframes up {
  0%,
  20%,
  50%,
  80%,
  100% {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px); }
  40% {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px); }
  60% {
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

